.navbar {
  position: fixed; /* Make it stick/fixed */
  top: 0; /* Stay on top */
  width: 100%; /* Full width */
  -webkit-transition: top 0.5s;
  transition: top 0.5s;
  z-index: 1; /* have navbar above everything, always */
}
.navbar {
  background-color: #5a2a3a !important;
}
